/* Header section */
#header {
    position: fixed;
    z-index: 5;
    width: 100%;
    height: auto;
}

#header .header {
    background-color: rgba(31, 30, 30, 0.24);
}

#header .nav-bar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100vw;
    height: 8vh;
    color: #fff;
    color: red;
    padding: 0px 20px;
    background-color: var(--bs-dark);

}

#header .nav-bar span {
    color: crimson;
    padding: 0;
    margin: 0;
}

#header .nav-list ul {
    list-style: none;
    position: absolute;
    width: 100vw;
    height: 100vh;
    left: 100%;
    top: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    z-index: 1;
    overflow-x: hidden;
    transition: .5s ease left;
    /* background-color: rgb(31, 30, 30); */
    background-color: var(--bs-dark);
}

#header .nav-list ul.active {
    left: 0%;
    /* justify-content: flex-start; */
}

#header .nav-list ul a {
    font-size: .7rem;
    font-weight: 300;
    letter-spacing: .2rem;
    text-decoration: none;
    color: white;
    text-transform: uppercase;
    padding: 20px;
    display: block;
}

#header .nav-list ul a::after {
    content: attr(data-after);
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0);
    color: rgba(240, 248, 255, 0.021);
    font-size: 13rem;
    letter-spacing: 50px;
    z-index: 1;
    transition: .3s ease letter-spacing;
}

#header .nav-list ul li:hover a::after {
    transform: translate(-50%, -50%) scale(1);
    letter-spacing: initial;
}

#header .nav-list ul li:hover a {
    color: crimson;
}

#header .hamburger {
    height: 50px;
    width: 60px;
    display: inline-block;
    border: 1px solid crimson;
    border-radius: 5px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    cursor: pointer;
    transform: scale(.5);
    /* margin-right: 20px; */
}

#header .hamburgerafter {
    position: absolute;
    content: '';
    height: 100%;
    width: 100%;
    border-radius: 50%;
    border: 3px solid white;
}

#header .hamburger .bar {
    height: 2px;
    width: 30px;
    position: relative;
    background-color: white;
    z-index: -1;
}

#header .hamburger .bar::after,
#header .hamburger .bar::before {
    content: '';
    position: absolute;
    height: 100%;
    width: 100%;
    left: 0;
    background-color: white;
    transition: .3s ease;
    transition-property: top, bottom;
}

#header .hamburger .bar::after {
    top: 8px;
    translate: 20deg;
}

#header .hamburger .bar::before {
    bottom: 8px;
    /* color: red; */
}

#header .hamburger.active .bar::before {
    bottom: 0;
    /* color: red; */
}

#header .hamburger.active .bar::after {
    top: 0;
}

/* End Header section */

