/*  */
a {
    color: #fff;
    /* color: red; */
    text-decoration: none;
    padding: 0;
    margin: 0;
    /* background-color: red; */
}

a:hover {
    color: crimson;
    border-bottom: 1px solid crimson;
    margin-bottom: 10px;
    transition: .3s ease-in-out;
    transition-property: background-color, color;
    transition-property: margin-bottom, color;


}

@media screen and (min-width: 320px) and (max-width: 560px) {
    a:hover {
        background-color: #1c0a0a;
        /* background-color: #fff; */
        margin-bottom: 0;
        /* margin-right: 10px; */
        margin-left: 10px;
        padding-left: 10px;
        padding-right: 10px;
        border-left: 1px solid crimson;
        color:#1c0a0a;
        color: #fff ;
        background-color: rgba(10,21,47, 0.9);
        /* background-color: crimson; */
        border-bottom: 0;
        transition: .3s ease-in-out;
        transition-property: margin, border, background-color, color;
        overflow: none;
        
    }
 
}

